function randomArticle(bias) {
    let articleOfBias = LOSources.filter((source) => (source.bias === bias));
    return articleOfBias[Math.floor(Math.random() * (articleOfBias.length))].name;
}


export function getURL(sourceName) {
    let today = new Date()
    let yesterday = new Date(today)

    yesterday.setDate(yesterday.getDate() - 5)

    let dateString = new Date(yesterday.getTime() - (yesterday.getTimezoneOffset() * 60000 ))
        .toISOString()
        .split("T")[0];
    return beginURL + apiKey + "&locale=us" + "&published_after=" + dateString + "&domains=" + sourceName;
}
